html, body, .scroll-div, #react-root {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

body {
	overflow: hidden;
}

.scroll-div {
	overflow: auto;
}

input[type="number"] {
    width: 3.5em;
}


/* Bootstrap overrides */
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
    font-weight: 400;
}

h1, .h1 {
    font-size: 2rem;
}